* {
  --mainBG: #151515;
  --accent: #C41818;
  --tertiary: #C43F3F;
  --text-color: #ffffff;
  color: var(--text-color);
}

html, body {
  max-width: 100%;
  overflow-x: hidden;
  background-color: var(--mainBG);
  color: var(--text-color)

}

h2 {
  color: var(--tertiary);
  text-align: center;
  text-transform: uppercase;
  font-family: 'Iceland';
  font-size: 3.75em;
}


.App {
  text-align: center;
  overflow: auto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
